import { ref } from "vue";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";
import { updatePluginData } from "./updatePluginData.js";

export const updating = ref(false);

/**
 * Updates given plugin
 *
 */
export const updatePlugin = async (plugin, emit) => {
  updating.value = true;
  const slug = plugin.slug.split("/")[0];

  const args = { endpoint: `uixpress/v1/plugin/update/${slug}`, params: {}, type: "POST" };
  const response = await lmnFetch(args);
  updating.value = false;

  if (!response) return false;

  notify({ title: __("Plugin updated", "uixpress"), message: plugin.Title, type: "success" });
  //emit("update", { has_update: false, Version: plugin.new_version });
  updatePluginData(plugin, { has_update: false, Version: plugin.new_version });

  return true;
};
