<script setup>
import { ref, defineProps, defineEmits, computed, defineModel, defineExpose } from "vue";

import AppButton from "@/components/utility/app-button/index.vue";
import Confirm from "@/components/utility/confirm/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";
import PluginActions from "./plugin-actions.vue";
import PluginCardBanner from "./plugin-card-banner.vue";
import AppCheckBox from "./checkbox.vue";

// Funcs
import { notify } from "@/assets/js/functions/notify.js";
import { updatePlugin } from "./updatePlugin.js";
import { deactivatePlugin } from "./deactivatePlugin.js";
import { activatePlugin } from "./activatePlugin.js";
import { deletePlugin } from "./deletePlugin.js";
import { autoUpdate } from "./autoUpdate.js";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

const props = defineProps(["plugin", "hideSelect"]);
const emit = defineEmits(["update"]);
const selected = defineModel();
const pluginData = ref({});
const updating = ref(false);
const deactivating = ref(false);
const activating = ref(false);
const deleting = ref(false);
const confirm = ref(null);
const autoUpdating = ref(false);

/**
 * Gets the best available icon for a plugin
 */
const getPluginIcon = computed(() => {
  if (props.plugin?.icons) {
    const icons = props.plugin.icons;
    return icons["2x"] || icons["1x"] || icons.default;
  }
  return `https://www.google.com/s2/favicons?domain=${props.plugin.PluginURI || props.plugin.AuthorURI}&sz=400`;
});

/**
 * Gets the banner image for a plugin
 */
const getPluginBanner = computed(() => {
  if (props.plugin?.banners) {
    const banners = props.plugin.banners;
    return banners["low"] || banners["high"] || null;
  }
  return null;
});

const returnActivePlugins = computed(() => {
  let list = [];
  for (let slug in appStore.state.pluginsList) {
    const plugin = appStore.state.pluginsList[slug];

    // Only return active plugins
    if (!plugin.active || plugin.deleted) continue;

    // format plugin
    plugin.slug = slug;

    list.push(plugin);
  }

  return list;
});

const hasRequiredPlugins = computed(() => {
  if (!props.plugin.RequiresPlugins) return true;
  const requiredPlugins = props.plugin.RequiresPlugins.split(",");

  // No plugin requirements
  if (!requiredPlugins.length) return true;

  for (let slug of requiredPlugins) {
    slug = slug.trim();

    if (!slug) continue;
    const activePlugin = returnActivePlugins.value.find((item) => item.splitSlug == slug);

    // Required plugin is not active
    if (!activePlugin) {
      return false;
    }
  }
  return true;
});

/**
 * Fetches extended plugin data from WordPress.org with caching
 */
const fetchPluginData = async () => {
  // Data has already been fetched for this instance
  if (props.plugin.banners || props.plugin.icons) {
    return;
  }

  const splitSlug = props.plugin.slug.split("/");
  const slug = splitSlug[0];

  // Check cache first
  const cached = localStorage.getItem(`plugin_data_${slug}`);
  if (cached) {
    try {
      const { data, timestamp } = JSON.parse(cached);
      const now = new Date().getTime();
      const age = now - timestamp;

      // If cache is less than 24 hours old (24 * 60 * 60 * 1000 = 86400000 ms)
      if (age < 86400000) {
        emit("update", {
          banners: data.banners,
          icons: data.icons,
          tags: data.tags,
          notInRepository: data.notInRepository,
        });
        return;
      } else {
        // Cache expired, remove it
        localStorage.removeItem(`plugin_data_${slug}`);
      }
    } catch (error) {
      // Invalid cache data, remove it
      localStorage.removeItem(`plugin_data_${slug}`);
      console.error("Error parsing cached plugin data:", error);
    }
  }

  // Fetch fresh data
  try {
    const response = await fetch(`https://api.wordpress.org/plugins/info/1.2/?action=plugin_information&slug=${slug}&fields=icons`);

    if (!response.ok) {
      emit("update", { notInRepository: true });

      cachePluginInfo({ notInRepository: true }, slug);
      return;
    }

    const data = await response.json();

    cachePluginInfo(data, slug);

    emit("update", {
      banners: data.banners,
      icons: data.icons,
      tags: data.tags,
    });
  } catch (error) {
    emit("update", { notInRepository: true });
    cachePluginInfo({ notInRepository: true }, slug);
    console.error(`Error fetching plugin data for ${slug}:`, error);
    return null;
  }
};

const cachePluginInfo = (data, slug) => {
  // Cache the fresh data
  localStorage.setItem(
    `plugin_data_${slug}`,
    JSON.stringify({
      data: {
        banners: data.banners,
        icons: data.icons,
        tags: data.tags,
        notInRepository: data.notInRepository,
      },
      timestamp: new Date().getTime(),
    })
  );
};

const updatePluginBySlug = async () => {
  updating.value = true;

  await updatePlugin(props.plugin, emit);

  updating.value = false;
};

const deactivatePluginBySlug = async () => {
  deactivating.value = true;

  await deactivatePlugin(props.plugin, emit);

  deactivating.value = false;
};

const activatePluginBySlug = async () => {
  activating.value = true;

  await activatePlugin(props.plugin, emit);

  activating.value = false;
};

const autoUpdateBySlug = async () => {
  autoUpdating.value = true;
  await autoUpdate(props.plugin, emit);
  autoUpdating.value = false;
};

const deletePluginBySlug = async () => {
  deleting.value = true;
  await deletePlugin(props.plugin, emit);
  deleting.value = false;
};

const confirmPluginDeletion = async () => {
  // Confirm user intent
  const userResponse = await confirm.value.show({
    title: __("Are you sure?", "uixpress"),
    message: __("Are you sure you want to delete this plugin? ", "uixpress"),
    okButton: __("Yes delete it", "uixpress"),
  });

  // Bailed by user
  if (!userResponse) return;

  deletePluginBySlug();
};

const toggleSelected = () => {
  if (selected.value.includes(props.plugin.slug)) {
    const index = selected.value.findIndex((slug) => slug == props.plugin.slug);
    selected.value.splice(index, 1);
  } else {
    selected.value.push(props.plugin.slug);
  }
};

const isSelected = computed(() => {
  return selected.value.includes(props.plugin.slug);
});

fetchPluginData();
defineExpose({ updatePluginBySlug, activatePluginBySlug, deactivatePluginBySlug, deletePluginBySlug });
</script>

<template>
  <div class="rounded-lg border border-zinc-200 dark:border-zinc-700 bg-white dark:bg-zinc-900 flex flex-col overflow-hidden shadow relative group cursor-pointer" @click="toggleSelected">
    <div class="w-full aspect-[3.089] bg-zinc-100 border-b border-zinc-200 dark:border-zinc-700 dark:bg-zinc-800 overflow-hidden relative">
      <!-- Show banner if available -->
      <img v-if="getPluginBanner" :src="getPluginBanner" class="w-full h-full object-cover object-center" />

      <PluginCardBanner v-else />

      <div class="absolute top-0 right-0 p-3 z-[99]" @click.prevent.stop>
        <PluginActions
          :plugin="plugin"
          @deactivate="deactivatePluginBySlug"
          @activate="activatePluginBySlug"
          @update="updatePluginBySlug"
          @delete="confirmPluginDeletion"
          @autoupdate="autoUpdateBySlug"
        />
      </div>

      <div class="absolute top-0 left-0 p-3 z-[99] group-hover:opacity-100 transition-opacity" :class="isSelected ? '' : 'opacity-0'" v-if="!hideSelect">
        <AppCheckBox :isactive="isSelected" />
      </div>

      <div class="absolute bottom-0 right-0 left-0 h-2/3 bg-gradient-to-t from-zinc-900/20 to-transparent"></div>
    </div>
    <div class="p-4 pt-0 -mt-3 flex flex-col gap-2 items-start grow z-[1]">
      <div class="rounded-lg bg-white dark:bg-zinc-900 p-1 -ml-1 -mt-1">
        <div class="rounded-md bg-indigo-600 aspect-square h-10 overflow-hidden">
          <img :src="getPluginIcon" class="w-full h-full object-cover" />
        </div>
      </div>
      <div class="text-zinc-900 dark:text-zinc-100 font-semibold" v-html="plugin.Name"></div>
      <div v-if="hasRequiredPlugins" class="text-zinc-500 dark:text-zinc-400 grow line-clamp-3" v-html="plugin.Description"></div>
      <div v-else class="bg-rose-500/20 rounded-lg p-2 text-xs flex flex-col gap-1">
        <span>{{ __("Requires other plugins to activate:", "uixpress") }}</span>
        <ul class="list-disc pl-3">
          <li v-for="sub in plugin.RequiresPlugins.split(',')">{{ sub }}</li>
        </ul>
      </div>
    </div>
    <div class="border-t border-zinc-200 dark:border-zinc-700 p-4 flex flex-row place-content-between items-center" @click.prevent.stop>
      <div>
        <AppButton type="primary" v-if="plugin.has_update" class="text-sm" :loading="updating" @click="updatePluginBySlug"
          >{{ __("Update", "uixpress") }} <span class="text-xs">(v{{ plugin.new_version }})</span>
        </AppButton>
        <AppButton type="default" v-else-if="!plugin.has_update && plugin.active" class="text-sm" :loading="deactivating" @click="deactivatePluginBySlug">{{ __("Deactivate", "uixpress") }}</AppButton>
        <AppButton type="default" v-else-if="!plugin.has_update && !plugin.active" class="text-sm" :loading="activating" @click="activatePluginBySlug" :disabled="!hasRequiredPlugins">{{
          __("Activate", "uixpress")
        }}</AppButton>
      </div>

      <div class="flex flex-row items-center gap-2">
        <div
          class="text-zinc-500 text-xs"
          :class="plugin.active ? 'text-emerald-700 px-2 border border-emerald-300 bg-emerald-100/50 dark:bg-emerald-700/20 dark:text-emerald-500 rounded dark:border-emerald-600' : ''"
        >
          v{{ plugin.Version }}
        </div>
      </div>
    </div>

    <Confirm ref="confirm" />

    <div class="absolute top-0 left-0 right-0 bottom-0 bg-zinc-900/50 flex flex-col items-center place-content-center justify-center z-[100]" v-if="deleting || autoUpdating">
      <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
        <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
        <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
      </svg>
    </div>
  </div>
</template>
