import { ref } from "vue";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";
import { updatePluginData } from "./updatePluginData.js";

export const activating = ref(false);

/**
 * Updates given plugin
 *
 */
export const activatePlugin = async (plugin, emit) => {
  activating.value = true;

  const slug = plugin.slug.split("/")[0];

  const args = { endpoint: `uixpress/v1/plugin/activate/${slug}`, params: {}, type: "POST" };
  const response = await lmnFetch(args);

  activating.value = false;

  if (!response) return false;

  notify({ title: __("Plugin activated", "uixpress"), message: plugin.Title, type: "success" });
  //emit("update", { active: true, action_links: response.data.action_links });
  updatePluginData(plugin, { active: true, action_links: response.data.action_links });

  // Dispatch event to allow other uiXpress components update correctly
  const pluginEvent = new CustomEvent("uipx-plugin-activated", { detail: slug });
  document.dispatchEvent(pluginEvent);

  return true;
};
