import { ref } from "vue";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";
import { updatePluginData } from "./updatePluginData.js";

export const deleting = ref(false);

/**
 * Updates given plugin
 *
 */
export const deletePlugin = async (plugin, emit) => {
  deleting.value = true;
  const slug = plugin.slug.split("/")[0];

  const args = { endpoint: `uixpress/v1/plugin/delete/${slug}`, params: {}, type: "DELETE" };
  const response = await lmnFetch(args);

  deleting.value = false;

  if (!response) return false;

  notify({ title: __("Plugin deleted", "uixpress"), message: plugin.Title, type: "success" });
  //emit("update", { deleted: true });
  updatePluginData(plugin, { deleted: true });

  return true;
};
