<script setup>
import { ref, defineProps, onMounted, onUnmounted, nextTick, defineEmits, computed } from "vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";

// Funcs
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";

const props = defineProps(["plugin"]);
const emit = defineEmits(["update", "delete", "activate", "deactivate"]);
const visible = ref(false);
const buttonRef = ref(null);
const dropdownRef = ref(null);
const updating = ref(false);

// Position the dropdown
const updateDropdownPosition = () => {
  if (!buttonRef.value || !dropdownRef.value || !visible.value) return;
  const buttonRect = buttonRef.value.getBoundingClientRect();

  // Position 16px below button and right-aligned
  dropdownRef.value.style.top = `${buttonRect.bottom + 6}px`;
  dropdownRef.value.style.right = `${window.innerWidth - buttonRect.right}px`;
};

// Update position when window resizes
const handleResize = () => {
  updateDropdownPosition();
};

// Modified click outside handler
const handleClickOutside = (event) => {
  if (!buttonRef.value || !dropdownRef.value) return;

  // Get the click path (composedPath includes shadow DOM)
  const path = event.composedPath();

  // Check if click path includes either the button or dropdown
  if (!path.includes(buttonRef.value) && !path.includes(dropdownRef.value)) {
    visible.value = false;
  }
};

const modifyPlugin = (action) => {
  // Close dropdown
  visible.value = false;
  emit(action);
};

const returnShortSlug = computed(() => {
  const splitSlug = props.plugin.slug.split("/");
  return splitSlug[0];
});

onMounted(() => {
  window.addEventListener("resize", handleResize);
  window.addEventListener("mousedown", handleClickOutside);
});

onUnmounted(() => {
  window.removeEventListener("resize", handleResize);
  window.removeEventListener("mousedown", handleClickOutside);
});

// Watch for visibility changes to update position
const toggleDropdown = () => {
  visible.value = !visible.value;

  if (visible.value) {
    nextTick(() => {
      updateDropdownPosition();
    });
  }
};
</script>

<template>
  <div class="relative" ref="buttonRef">
    <button class="p-1 rounded-lg border border-zinc-200 dark:border-zinc-700 bg-white dark:bg-zinc-900 text-zinc-500 hover:text-zinc-900 dark:hover:text-zinc-100" @click="toggleDropdown">
      <AppIcon icon="more_vert" />
    </button>
  </div>
  <div v-if="visible" ref="dropdownRef" class="fixed z-[99] rounded-lg shadow border border-zinc-200 dark:border-zinc-700 bg-white dark:bg-zinc-900 py-2 px-2 flex flex-col min-w-[160px]">
    <RouterLink
      :to="`/plugin-search/${returnShortSlug}`"
      class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1"
      v-if="!plugin.notInRepository"
      @click="visible = false"
    >
      {{ __("View plugin info", "uixpress") }}
    </RouterLink>

    <RouterLink
      :to="`/plugin-performance/${returnShortSlug}`"
      class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1"
      v-if="plugin.active"
      @click="visible = false"
    >
      {{ __("Plugin performance", "uixpress") }}
    </RouterLink>

    <div v-if="!plugin.notInRepository || plugin.active" class="border-top border-b border-zinc-200 dark:border-zinc-700 my-2 mx-1"></div>

    <div class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1" v-if="plugin.has_update" @click="modifyPlugin('update')">
      {{ __("Update", "uixpress") }}
    </div>
    <div class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1" v-if="!plugin.active" @click="modifyPlugin('activate')">
      {{ __("Activate", "uixpress") }}
    </div>
    <div class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1" v-if="plugin.active" @click="modifyPlugin('deactivate')">
      {{ __("Deactivate", "uixpress") }}
    </div>

    <div class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1" @click="modifyPlugin('autoupdate')" v-if="!plugin.auto_update_enabled">
      {{ __("Enable auto updates", "uixpress") }}
    </div>

    <div class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1" @click="modifyPlugin('autoupdate')" v-if="plugin.auto_update_enabled">
      {{ __("Disable auto updates", "uixpress") }}
    </div>

    <!-- Action links -->
    <template v-if="plugin.active">
      <div class="border-top border-b border-zinc-200 dark:border-zinc-700 my-2 mx-1" v-if="plugin.action_links.length"></div>
      <template v-for="link in plugin.action_links">
        <a :href="link.url" class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1" v-html="link.text"></a>
      </template>
    </template>

    <!-- Plugin links -->
    <div class="border-top border-b border-zinc-200 dark:border-zinc-700 my-2 mx-1"></div>
    <a
      v-if="plugin.AuthorURI"
      :href="plugin.AuthorURI"
      target="_BLANK"
      @click.stop
      class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1 flex flex-row items-center gap-2 place-content-between group/link"
    >
      <span v-html="plugin.Author"> </span>
      <AppIcon icon="open_new" class="opacity-0 group-hover/link:opacity-100 transition-opacity" />
    </a>
    <a
      v-if="plugin.PluginURI"
      :href="plugin.PluginURI"
      target="_BLANK"
      @click.stop
      class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1 flex flex-row items-center gap-2 place-content-between group/link"
    >
      <span>{{ __("Plugin site", "uixpress") }}</span>
      <AppIcon icon="open_new" class="opacity-0 group-hover/link:opacity-100 tranition-opacity" />
    </a>

    <!-- Delete -->
    <div class="border-top border-b border-zinc-200 dark:border-zinc-700 my-2 mx-1" v-if="!plugin.active"></div>
    <div class="rounded-lg hover:bg-zinc-100 dark:hover:bg-zinc-800 transition-all cursor-pointer px-2 py-1 text-red-500 hover:text-red-700" v-if="!plugin.active" @click="modifyPlugin('delete')">
      {{ __("Delete", "uixpress") }}
    </div>
  </div>
</template>
