<script setup>
import { ref, watch, nextTick, computed, watchEffect, defineAsyncComponent, onMounted, defineEmits } from "vue";
import { ShadowRoot } from "vue-shadow-dom";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Funcs
import { inSearch } from "@/assets/js/functions/inSearch.js";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { useDarkMode } from "./src/useDarkMode.js";
const { isDark } = useDarkMode();

// Comps
import LoadingIndicator from "@/components/utility/loading-indicator/index.ts";
import Notifications from "@/components/utility/notifications/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";
import AppInput from "@/components/utility/text-input/index.vue";
import Tabs from "@/components/utility/tabs/index.vue";
import PluginCard from "./src/plugin-card.vue";
import Confirm from "@/components/utility/confirm/index.vue";
import OffCanvas from "@/components/utility/offcanvas/index.vue";
import PluginRepo from "./src/plugin-repo.vue";
import PluginStats from "./src/plugin-stats.vue";

// Functions
import { notify } from "@/assets/js/functions/notify.js";

// Refs
const adoptedStyleSheets = ref(new CSSStyleSheet());
const emit = defineEmits(["update"]);
const pluginData = ref({});
const loading = ref(false);
const search = ref("");
const activeFilter = ref("all");
const selected = ref([]);
const updating = ref(false);
const activating = ref(false);
const deactivating = ref(false);
const deleting = ref(false);
const confirm = ref(null);
const uploading = ref(false);
const pluginupload = ref(null);
const pluginCards = ref({});
const pluginrepo = ref(null);

const filterOptions = computed(() => {
  const pluginArray = Object.values(appStore.state.pluginsList);
  const activeCount = pluginArray.filter((plugin) => plugin.active);
  const inActiveCount = pluginArray.filter((plugin) => !plugin.active);
  const updateCount = pluginArray.filter((plugin) => plugin.has_update);
  const autoUpdatesEnabled = pluginArray.filter((plugin) => plugin.auto_update_enabled);
  const autoUpdatesDisabled = pluginArray.filter((plugin) => !plugin.auto_update_enabled);

  return {
    all: { label: __("All", "uixpress"), value: "all", count: pluginArray.length },
    active: { label: __("Active", "uixpress"), value: "active", count: activeCount.length },
    inactive: { label: __("Inactive", "uixpress"), value: "inactive", count: inActiveCount.length },
    update: { label: __("Has update", "uixpress"), value: "update", count: updateCount.length },
    auto_updates_enabled: { label: __("Auto updates enabled", "uixpress"), value: "auto_updates_enabled", count: autoUpdatesEnabled.length },
    auto_updates_disabled: { label: __("Auto updates disabled", "uixpress"), value: "auto_updates_disabled", count: autoUpdatesDisabled.length },
  };
});

const meetsFilter = (plugin) => {
  if (activeFilter.value === "all") return true;
  if (activeFilter.value === "active" && plugin.active) return true;
  if (activeFilter.value === "inactive" && !plugin.active) return true;
  if (activeFilter.value === "update" && plugin.has_update) return true;
  if (activeFilter.value === "auto_updates_enabled" && plugin.auto_update_enabled) return true;
  if (activeFilter.value === "auto_updates_disabled" && !plugin.auto_update_enabled) return true;
  return false;
};

/**
 * Injects styles into shadow root
 */
const setStyles = () => {
  let appStyleNode = document.querySelector("#uixpress-plugins-css");
  if (!appStyleNode) {
    appStyleNode = manuallyAddStyleSheet();
    // Wait for stylesheet to load
    appStyleNode.onload = () => {
      const appStyles = appStyleNode.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
  } else {
    const appStyles = appStyleNode.sheet;
    for (const rule of [...appStyles.cssRules].reverse()) {
      adoptedStyleSheets.value.insertRule(rule.cssText);
    }
  }
};

const manuallyAddStyleSheet = () => {
  var link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = `${appStore.state.siteURL}/wp-content/plugins/uixpress/app/dist/assets/styles/plugins.css`;
  document.head.appendChild(link);
  return link;
};

const updatePluginData = (plugin, data) => {
  appStore.state.pluginsList[plugin.slug] = { ...appStore.state.pluginsList[plugin.slug], ...data };
};

const returnActivePlugins = computed(() => {
  let list = [];
  for (let slug in appStore.state.pluginsList) {
    const plugin = appStore.state.pluginsList[slug];

    // Only return active plugins
    if (!plugin.active || plugin.deleted) continue;

    // format plugin
    plugin.slug = slug;

    if (!inSearch(search.value, JSON.stringify(plugin))) continue;

    list.push(plugin);
  }

  return list.filter((item) => meetsFilter(item));
});

const returnInActivePlugins = computed(() => {
  let list = [];
  for (let slug in appStore.state.pluginsList) {
    const plugin = appStore.state.pluginsList[slug];

    // Only return active plugins
    if (plugin.active || plugin.deleted) continue;

    // Meets filters
    if (!meetsFilter(plugin)) continue;

    // format plugin
    plugin.slug = slug;

    if (!inSearch(search.value, JSON.stringify(plugin))) continue;

    list.push(plugin);
  }

  return list.filter((item) => meetsFilter(item));
});

const selectionHasInactive = computed(() => {
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    if (!pluginObject.active) return true;
  }
  return false;
});

const selectionHasActive = computed(() => {
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    if (pluginObject.active) return true;
  }
  return false;
});

const selectionHasUpdate = computed(() => {
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    if (pluginObject.has_update) return true;
  }
  return false;
});

const batchUpdatePlugins = async () => {
  const selectedPlugins = [];
  updating.value = true;
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    // Only attempt update if plugin has an update
    if (!pluginObject.has_update) continue;

    await pluginCards.value[slug].updatePluginBySlug();
  }
  updating.value = false;
  selected.value = [];
};

const batchActivatePlugins = async () => {
  const selectedPlugins = [];
  activating.value = true;
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    // Only attempt activate if plugin is deactivated
    if (pluginObject.active) continue;

    await pluginCards.value[slug].activatePluginBySlug();
  }
  activating.value = false;
  selected.value = [];
};

const batchDeactivatePlugins = async () => {
  const selectedPlugins = [];
  deactivating.value = true;
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    // Only attempt deactivate if plugin is active
    if (!pluginObject.active) continue;

    await pluginCards.value[slug].deactivatePluginBySlug();
  }
  deactivating.value = false;
  selected.value = [];
};

const batchDeletePlugins = async () => {
  // Confirm user intent
  const userResponse = await confirm.value.show({
    title: __("Are you sure?", "uixpress"),
    message: __("Are you sure you want to delete these plugins? ", "uixpress"),
    okButton: __("Yes delete them", "uixpress"),
  });

  // Bailed by user
  if (!userResponse) return;

  const selectedPlugins = [];
  deleting.value = true;
  for (let slug of selected.value) {
    const pluginObject = appStore.state.pluginsList[slug];
    // Only attempt deactivate if plugin is active
    //if (pluginObject.active) continue;

    await pluginCards.value[slug].deletePluginBySlug();
  }
  deleting.value = false;
  selected.value = [];
};

const selectAllPlugins = () => {
  selected.value = [];
  for (let key in appStore.state.pluginsList) {
    const plugin = appStore.state.pluginsList[key];
    selected.value.push(plugin.slug);
  }
};

const allSelected = computed(() => {
  return selected.value.length === Object.keys(appStore.state.pluginsList).length;
});

const handlePluginUpload = async (evt) => {
  uploading.value = true;

  const files = evt.target.files;
  if (!files || !files[0]) {
    throw new Error("No file selected");
  }

  const formData = new FormData();
  formData.append("plugin_zip", files[0]);

  const args = {
    endpoint: "uixpress/v1/plugin/install",
    params: {},
    type: "POST",
    data: formData,
    isFormData: true,
  };

  const response = await lmnFetch(args);

  uploading.value = false;

  if (!response) return;

  // Handle successful upload
  notify({
    title: "Success",
    message: response.message,
    type: "success",
  });

  appStore.state.pluginsList[response.data.plugin.slug] = response.data.plugin;

  // Clear the file input
  //evt.target.value = "";

  await nextTick();

  // Confirm user intent
  const userResponse = await confirm.value.show({
    title: __("Activate plugin?", "uixpress"),
    message: __("The plugin was succesfully uploaded, would you like to activate it? ", "uixpress"),
    okButton: __("Activate", "uixpress"),
  });

  // Bailed by user
  if (userResponse) {
    pluginCards.value[response.data.plugin.slug].activatePluginBySlug();
  }
};

setStyles();
</script>

<template>
  <component is="style"> html{font-size:14px;}#wpcontent{padding:0}</component>
  <ShadowRoot tag="div" :adopted-style-sheets="[adoptedStyleSheets]" class="uipx-normalize">
    <component is="style"> :root{font-size:14px}#wpcontent{padding:0}</component>
    <div
      class="@container bg-white dark:bg-zinc-900 uipx-normalize font-sans dark:bg-zinc-900 bg-white"
      style="min-height: calc(100dvh - var(--wp-admin--admin-bar--height))"
      :class="isDark ? 'dark' : ''"
    >
      <Notifications />
      <div class="translate-x-0">
        <LoadingIndicator :height="3" />
      </div>
      <div class="flex flex-row place-content-between col-span-3 items-center p-6">
        <div class="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">{{ __("Plugins", "uixpress") }}</div>
        <div class="flex flex-row items-stretch gap-2">
          <AppInput v-model="search" icon="search" :placeholder="__('Search', 'uixpress')" />
          <RouterLink to="/plugin-search" class="flex flex-row items-stretch">
            <AppButton type="default" class="text-sm">
              {{ __("Add new", "uixpress") }}
            </AppButton>
          </RouterLink>
          <AppButton v-if="!allSelected" type="default" class="text-sm" @click="selectAllPlugins">
            {{ __("Select all", "uixpress") }}
          </AppButton>
          <AppButton v-else type="default" class="text-sm" @click="selected = []">
            {{ __("Deselect all", "uixpress") }}
          </AppButton>

          <AppButton type="primary" class="text-sm" @click="pluginupload.click($event)" :loading="uploading">
            {{ __("Upload plugin", "uixpress") }}
            <input type="file" ref="pluginupload" multiple accept=".zip" @change="handlePluginUpload($event)" class="hidden" />
          </AppButton>
        </div>
      </div>

      <!-- Tabs -->
      <div v-if="1 == 2" class="flex flex-row items-center px-6 pb-6 place-content-between">
        <Tabs :options="filterOptions" v-model="activeFilter" />
        <AppInput v-model="search" icon="search" :placeholder="__('Search', 'uixpress')" />
      </div>

      <div class="flex flex-row gap-2 mb-6 max-w-full overflow-auto px-6">
        <div
          v-for="tab in filterOptions"
          class="p-3 rounded-lg flex flex-col gap-1 grow cursor-pointer transition-all"
          :class="tab.value === activeFilter ? 'bg-indigo-100 dark:bg-indigo-800/60' : 'bg-zinc-100 dark:bg-zinc-800 hover:bg-indigo-100 dark:hover:bg-indigo-800/60'"
          @click="activeFilter = tab.value"
        >
          <div class="text-sm whitespace-nowrap">{{ tab.label }}</div>
          <div class="font-semibold text-xl">{{ tab.count }}</div>
        </div>
      </div>

      <PluginStats v-if="1 == 2" />

      <!-- Active plugins -->
      <div class="grid @[300px]:grid-cols-2 @[500px]:grid-cols-3 @[900px]:grid-cols-4 @[1100px]:grid-cols-5 gap-6 p-6" v-if="returnActivePlugins.length">
        <div class="flex flex-row place-content-between col-span-full items-center col-span-4">
          <div class="text-lg text-zinc-900 dark:text-zinc-100">{{ __("Active plugins", "uixpress") }} ({{ returnActivePlugins.length }})</div>
        </div>
        <TransitionGroup name="pluginlist">
          <template v-for="(plugin, index) in returnActivePlugins" :key="index">
            <PluginCard
              :plugin="plugin"
              @update="(d) => updatePluginData(plugin, d)"
              :somekey="index"
              v-model="selected"
              :ref="
                (el) => {
                  pluginCards[plugin.slug] = el;
                }
              "
            />
          </template>
        </TransitionGroup>
      </div>

      <!-- Inactive plugins -->
      <div class="grid @[300px]:grid-cols-2 @[500px]:grid-cols-3 @[900px]:grid-cols-4 @[1100px]:grid-cols-5 gap-6 p-6" v-if="returnInActivePlugins.length">
        <div class="flex flex-row place-content-between col-span-full items-center col-span-4">
          <div class="text-lg text-zinc-900 dark:text-zinc-100">{{ __("Inactive plugins", "uixpress") }} ({{ returnInActivePlugins.length }})</div>
        </div>
        <TransitionGroup name="pluginlist">
          <template v-for="(plugin, index) in returnInActivePlugins" :key="index">
            <PluginCard
              :plugin="plugin"
              @update="(d) => updatePluginData(plugin, d)"
              :somekey="index"
              v-model="selected"
              :ref="
                (el) => {
                  pluginCards[plugin.slug] = el;
                }
              "
            />
          </template>
        </TransitionGroup>
      </div>

      <div class="flex flex-col gap-6 items-center p-16 mx-auto w-[460px]" v-if="!returnInActivePlugins.length && !returnActivePlugins.length">
        <AppIcon icon="folder_open" style="font-size: 5rem" />
        <span class="text-zinc-900 text-3xl text-center font-semibold">{{ __("No plugins found", "uixpress") }} :(</span>
        <span class="text-zinc-500 text-lg text-center">{{ __("Upload plugin zip files or search the repository", "uixpress") }}</span>
        <div class="place-content-between flex gap-6 flex-row">
          <slot name="links"></slot>
        </div>
      </div>

      <div class="fixed bottom-6 left-1/2 -translate-y-1/2 z-[101]">
        <Transition>
          <div class="flex flex-row items-center gap-2 p-2 bg-white dark:bg-zinc-900 border border-zinc-200 dark:border-zinc-700 rounded-xl shadow" v-if="selected.length">
            <AppButton type="primary" :disabled="!selectionHasInactive" @click="batchActivatePlugins" :loading="activating">{{ __("Activate", "uixpress") }}</AppButton>
            <AppButton type="warning" :disabled="!selectionHasActive" @click="batchDeactivatePlugins" :loading="deactivating">{{ __("Deactivate", "uixpress") }}</AppButton>
            <AppButton type="default" :disabled="!selectionHasUpdate" @click="batchUpdatePlugins" :loading="updating">{{ __("Update", "uixpress") }}</AppButton>

            <div class="h-6 border-r border-zinc-200 dark:border-zinc-700"></div>

            <AppButton type="transparent" @click="batchDeletePlugins" :loading="deleting"><AppIcon icon="delete" class="text-rose-600 text-xl" /></AppButton>

            <div class="h-6 border-r border-zinc-200 dark:border-zinc-700"></div>

            <AppButton type="transparent" @click="selected = []"><AppIcon icon="close" class="text-xl" /></AppButton>
          </div>
        </Transition>
      </div>
      <Confirm ref="confirm" />

      <component is="style">
        .pluginlist-enter-active, .pluginlist-leave-active { transition: all 0.5s ease; } .pluginlist-enter-from, .pluginlist-leave-to { opacity: 0; transform: translateX(30px); }
      </component>

      <RouterView id="sub-route" v-slot="{ Component }">
        <KeepAlive>
          <component :is="Component" />
        </KeepAlive>
      </RouterView>
    </div>
  </ShadowRoot>
</template>
